@import '../scss/sass_vars';

.appMain {
  height: 100vh;
  background-color: var(--main-background-color);
}

.main {
  position: relative;
  height: calc(100vh - 60px);
  padding: 0 5vw;
  overflow-x: hidden;

  @media screen and (max-width: $mobileBreakpoint) {
    padding: 0 10px;
  }
}

.notificationsWrapper {
  position: absolute;
  top: 0;
  right: 0;
  display: flex;
  flex-direction: column;
  gap: 5px;
  padding: 5px;
  z-index: 5;
}
