@import '../../scss/sass_vars';

.appBar {
  display: flex;
  align-items: stretch;
  padding: 0 10px 0 0;
  box-shadow: 1px 1px 5px 1px black;
  background-color: var(--main-background-color);

  @media screen and (max-width: $mobileBreakpoint)  {
    display: block;
  }
}

.controlsWrapper {
  display: flex;
  flex-grow: 1;
  justify-content: space-between;
  align-items: center;
  gap: 20px;
  padding: 10px 0 10px 0;
  list-style: none;

  & > :last-child {
    margin-left: auto;
  }
}

.logo {
  display: flex;
  align-items: center;
  padding: 0 67px 0 10px;
  background-image: linear-gradient(
    90deg,
    var(--color-accent) 15%,
    var(--main-background-color-alpha) 50%,
    transparent 60%
  );
  font-size: 25px;
}
